import React, { useState, useContext, createRef } from 'react';

import PartnerCard from './PartnerCard';
import leftArrow from '../../assets/images/arrow-backward.svg';
import rightArrow from '../../assets/images/arrow-forward.svg';
import { WindowPropertiesContext } from '../../store/WindowPropertiesStore';
import './Partners.css';

const Partners = () => {

    const [windowProperties] = useContext(WindowPropertiesContext);

    let isDesktop = windowProperties.width > 1024;
    const [isStart, setIsStart] = useState(true);

    const scrollRef = createRef();

    const handleRightClick = () => {
        scrollRef.current.scrollBy({ left: 2000, top: 0, behavior: 'smooth' })
        setIsStart(false);
    };

    const handleLeftClick = () => {
        scrollRef.current.scrollBy({ left: -2000, top: 0, behavior: 'smooth' })
        setIsStart(true);
    };
    
    return <div className="partners relative">
        <h2>Наши партнеры:</h2>
        { isDesktop ? <div>
            { !isStart ? <img src={leftArrow} className="arrow-left pointer" onClick={handleLeftClick} alt="Левая стрелка" /> : <div className="hide" /> }
            { isStart ? <img src={rightArrow} className="arrow-right pointer" onClick={handleRightClick} alt="Правая стрелка" /> : <div className="hide" /> }
        </div> : <div className="hide" /> }
        <ul className="hs" ref={scrollRef}>
            <li><PartnerCard num={0} /></li>
            <li><PartnerCard num={1} /></li>
            <li><PartnerCard num={2} /></li>
            <li><PartnerCard num={3} /></li>
            <li><PartnerCard num={4} /></li>
            <li><PartnerCard num={5} /></li>
        </ul>
    </div>;
}

export default Partners;