import React, { useContext, useRef, useState } from 'react';
import { useChain } from 'react-spring';

import { CardStatesContext } from '../../store/CardStatesStore';
import { InputFieldsContext } from '../../store/InputFieldsStore';
import calendar from '../../assets/images/calendar.svg';
import Event from './Event';
import './Events.css';

const Events = () => {

    const [eventProperties, setDay] = useContext(CardStatesContext);
    const [inputValues, onNameChange, onEmailChange] = useContext(InputFieldsContext);
    const [honeyPass, setHoneyPass] = useState('');
    const [agree, setAgree] = useState(false);

    const onPasswordChange = (event) => {
        setHoneyPass(event.target.value);
    }

    const checkName = (name) => {
        if (name.length > 1) {
            return true;
        } else {
            return false;
        }
    }

    const checkEmail = (email) => {
        let re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if (re.test(email)) {
            return true;
        } else {
            return false;
        }
    }

    const checkEventSign = () => {
        const filteredArr = eventProperties.checkedCards.map(day => day.filter(ev => ev !== -1));
        if ((filteredArr[0].length !== 0) || (filteredArr[1].length !== 0) || (filteredArr[2].length !== 0)) {
            return true;
        } else {
            return false;
        }
    }

    const readyToBeSubmitted = (checkName(inputValues.name) && checkEmail(inputValues.email) && checkEventSign());

    const transRef1 = useRef()
    const transRef2 = useRef()
    const transRef3 = useRef()
    
    useChain(eventProperties.day === 0 ? [transRef2, transRef3, transRef1] : eventProperties.day === 1 ? [transRef1, transRef3, transRef2] : [transRef2, transRef1, transRef3], [0, 0, .2]);

    let firstDayStyle = 'events-header pointer';
    if (eventProperties.day === 0) {
        firstDayStyle = 'events-header violet-bg pointer white';
    }
    let secondDayStyle = 'events-header pointer';
    if (eventProperties.day === 1) {
        secondDayStyle = 'events-header grad-bg pointer white';
    }
    let thirdDayStyle = 'events-header pointer';
    if (eventProperties.day === 2) {
        thirdDayStyle = 'events-header blue-bg pointer white';
    }

    let buttonStyle = 'register-btn pointer border-radius flex-centered-vert';
    if (!readyToBeSubmitted) {
        buttonStyle = 'disabled-btn register-btn border-radius flex-centered-vert';
    }

    const submitButtonClickHandler = (name, email, honey) => {
        if (readyToBeSubmitted) {
            // setSubmitButtonClicked(!submitButtonClicked);
            // setTimeout(() => {
            //     complete();
            //     setSubmitButtonClicked(false);
            // }, 4500);
            if (honey === '') {
                fetch('http://localhost:5000/register', {
                    method: 'post',
                    headers: {'Content-Type': 'application/json'},
                    body: JSON.stringify({
                        name: name,
                        email: email
                    })
                });
            }
        }
    }

    let checkboxStyleClasses = 'checkbox white-bg unchecked';
    if (agree) {
        checkboxStyleClasses = 'checkbox white-bg checked';
    }

    const clickCheckmark = () => {
        setAgree(!agree);
    }

    return (
    <div className="relative overflow-hidden full-width margin-from-prev-comp">
        <h1 className="register-header text-centered">Регистрация</h1>
        <h2 className="register-sub-header text-centered">очных участников</h2>
        <div className="container" style={{marginBottom: '2.5em'}}>
                <div className="flex-column input-div">
                    <label>Введите имя и фамилию</label>
                    <input type="text" placeholder="Ваши имя и фамилия" className="sign-input border-radius" onChange={onNameChange} value={inputValues.name} required/>
                </div>
                <div className="flex-column input-div">
                    <label>Введите электронную почту</label>
                    <input type="text" placeholder="Ваш email" className="sign-input border-radius" onChange={onEmailChange} value={inputValues.email} required/>
                </div>
                {/* <div className="flex-centered-vert">
                    <div className={checkboxStyleClasses} onClick={clickCheckmark}>
                        <div className="checkbox-checkmark absolute" />
                    </div>
                    <p onClick={clickCheckmark}>Я согласен с условиями обработки персональных данных</p>
                </div> */}
                <input type="text" name="password" style={{position:'absolute', opacity: 0}} tabIndex="-1" autoComplete="off"  onChange={onPasswordChange} />
        </div>

        <div className="flex ev-cards-headers">
            <div className={firstDayStyle} onClick={() => setDay(0)} >
                <h2 className="text-centered">1 День</h2>
                {/* <h2 className="text-centered">Открытие</h2> */}
                {/* <p className="text-centered">Воскресенье<br />27 Октября 2019</p> */}
            </div>
            <div className={secondDayStyle} onClick={() => setDay(1)} >
                <h2 className="text-centered">2 День</h2>
                {/* <h2 className="text-centered">Первый день</h2> */}
                {/* <p className="text-centered">Суббота<br />3 Ноября 2019</p> */}
            </div>
            <div className={thirdDayStyle} onClick={() => setDay(2)} >
                <h2 className="text-centered">3 День</h2>
                {/* <h2 className="text-centered">Последний день</h2> */}
                {/* <p className="text-centered">Воскресенье<br />3 Ноября 2019</p> */}
            </div>
        </div>

        <Event dayNum={0} transRef={transRef1} />
        <Event dayNum={1} transRef={transRef2} />
        <Event dayNum={2} transRef={transRef3} />

        <div className="container" style={{marginTop: '3.5em'}}>
            <button className={buttonStyle} onClick={() => submitButtonClickHandler(inputValues.name, inputValues.email, honeyPass)}>{readyToBeSubmitted ? <img src={calendar} style={{width: 45, marginRight: 10}} /> : <div className="hide" />}Завершить регистрацию</button>
        </div>
    </div>
    );
}

export default Events;