import React from 'react';

import './About.css';

const About = () => {
    return (
        <div className="container">
            <div className="w-c">
                <div className="wave-cnt" />
                <div className="wave-container relative grad-bg">
                    <div>
                        <ul className="desktop-font line-height">
                            <li>
                                <p className="white">Эксперимент, японистика, вызов, встреча единомышленников – такой набор тэгов нам действительно подходит!</p>
                            </li>
                            <li>
                                <p className="white">Наш проект – уникальная попытка включить в крупное образовательное событие о Японии живой контакт между экспертами и любителями, простыми интересующимися и опытными деятелями.</p>
                            </li>
                            <li>
                                <p className="white">30 мероприятий о Средневековой Японии – от бесед с мастерами своего дела
                                    до лекций, от вака-баттла до кинопоказа.<br />Вы успеете выполнить несколько
                                    небольших заданий, выиграть призы, встретить вдохновлённых японолюбов,
                                    ознакомиться с открытиями российских японистов и их повседневной
                                    деятельностью.
                                </p>
                            </li>
                            <li>
                                <p className="white">Больше того, после Школы мы вас с этим багажом знаний не бросим.<br />Активные участники будут рекомендованы к включению в японоведческие
                                    проекты в качестве стажёров.<br />Многие сообщества как раз ищут энергичных ребят!
                                </p>
                            </li>
                            <li>
                                <p className="white">И всё вышеизложенное – совершенно бесплатно.</p>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default About;